// Diagnostic endpoint to check which database is being used
import { NextResponse } from 'next/server';
import { getDb } from '@/db/client';

export async function GET() {
  try {
    const db = getDb();
    const dbType = db.constructor.name;
    const hasDatabaseUrl = !!process.env.DATABASE_URL;
    
    // Try to query the database
    let endpointCount = 0;
    let logCount = 0;
    
    let queryError: string | undefined;
    try {
      const endpoints = await db.listEndpoints();
      endpointCount = endpoints.length;
      
      const logs = await db.listUsageLogs({ endpointId: 'ep_url_enrich_demo_v1' });
      logCount = logs.length;
    } catch (error) {
      queryError = error instanceof Error ? error.message : String(error);
    }
    
    // Try to get actual connection error details
    let connectionError: string | undefined;
    if (queryError) {
      connectionError = queryError;
    } else if (!hasDatabaseUrl) {
      connectionError = 'DATABASE_URL environment variable is not set';
    }
    
    return NextResponse.json({
      databaseType: dbType,
      hasDatabaseUrl,
      databaseUrl: hasDatabaseUrl ? '***set***' : 'not set',
      endpointCount,
      logCountForEndpoint: logCount,
      queryError: connectionError,
      message: hasDatabaseUrl 
        ? (queryError 
          ? `Using ${dbType} but connection failed: ${queryError}`
          : `Using ${dbType} (PostgreSQL) -  Connected`)
        : `Using ${dbType} (SQLite - default). Set DATABASE_URL to use PostgreSQL.`,
      nextSteps: connectionError 
        ? [
            '1. Verify DATABASE_URL is in .env.local (project root)',
            '2. Restart Next.js dev server (environment variables load on startup)',
            '3. Check PostgreSQL is running: Test connection with scripts/test-postgres-connection.ts',
            '4. Verify connection string format: postgresql://user:password@host:port/database'
          ]
        : logCount === 0 && hasDatabaseUrl
        ? ['Database connected but no logs found', 'Check if logs exist in database', 'Verify endpoint ID matches']
        : [],
      troubleshooting: !hasDatabaseUrl ? {
        step1: 'Add DATABASE_URL to .env.local file in project root',
        step2: 'Restart Next.js dev server (Ctrl+C then npm run dev)',
        step3: 'Verify with: npx tsx scripts/test-postgres-connection.ts',
      } : undefined,
    });
  } catch (error) {
    return NextResponse.json({
      error: error instanceof Error ? error.message : 'Unknown error',
    }, { status: 500 });
  }
}

